.bPlDashboardFSCheckoutForm {
	position: relative;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	iframe {
		width: 100%;
		min-height: 640px;
	}

	.bPlLoading {
		width: 100%;
		height: 100%;
		position: absolute;
		inset: 0;
		background-color: rgba(255, 255, 255, 0.75);
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 10;
		border-radius: 20px;
	}
}